<template>
  <div>
    <div class="alert alert-warning" role="alert">
     Em breve serão adicionados novos conteúdos em vídeo
    </div>
    <article class="main-page-content" lang="pt-BR">
      <h1>HTML: Linguagem de Marcação de Hipertexto</h1>
      <h2 id="tutoriais_para_iniciantes">
        <a
          href="#tutoriais_para_iniciantes"
          title="Permalink to Tutoriais para iniciantes"
          >Tutoriais para iniciantes</a
        >
      </h2>
      <div>
        <div></div>

        <p class="summary">
          <span class="seoSummary"
            ><strong>HTML</strong> (Linguagem de Marcação de HiperTexto) é o
            bloco de construção&nbsp;mais básico da web. Define o
            significado&nbsp;e a estrutura do conteúdo da web. Outras
            tecnologias além do HTML geralmente são usadas para descrever a
            aparência/apresentação&nbsp;(<a href="/pt-BR/docs/Web/CSS">CSS</a>)
            ou a funcionalidade/comportamento (<a
              href="/pt-BR/docs/Web/JavaScript"
              >JavaScript</a
            >) de uma página da web.</span
          >
        </p>

        <p>
          "Hipertexto"&nbsp;refere-se aos <em>links</em>&nbsp;que conectam
          páginas da Web entre si, seja dentro de um único site ou entre sites.
          Links são um aspecto fundamental da web. Ao carregar conteúdo na
          Internet e vinculá-lo a páginas criadas por outras pessoas, você se
          torna um participante ativo na world wide web.
        </p>

        <p>
          O HTML usa "Marcação"&nbsp;para anotar&nbsp;texto, imagem&nbsp;e
          outros conteúdos para exibição&nbsp;em um&nbsp;navegador da Web. A
          marcação&nbsp;HTML inclui&nbsp;"elementos" especiais,&nbsp;como
          <a href="/pt-BR/docs/Web/HTML/Element/head"
            ><code>&lt;head&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/title"
            ><code>&lt;title&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/body"
            ><code>&lt;body&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/header"
            ><code>&lt;header&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/footer"
            ><code>&lt;footer&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/article"
            ><code>&lt;article&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/section"
            ><code>&lt;section&gt;</code></a
          >, <a href="/pt-BR/docs/Web/HTML/Element/p"><code>&lt;p&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/div"><code>&lt;div&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/span"
            ><code>&lt;span&gt;</code></a
          >,&nbsp;<a href="/pt-BR/docs/Web/HTML/Element/img"
            ><code>&lt;img&gt;</code></a
          >,&nbsp;<a href="/pt-BR/docs/Web/HTML/Element/aside"
            ><code>&lt;aside&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/Audio"
            ><code>&lt;audio&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/canvas"
            ><code>&lt;canvas&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/datalist"
            ><code>&lt;datalist&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/details"
            ><code>&lt;details&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/embed"
            ><code>&lt;embed&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/nav"><code>&lt;nav&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/Output"
            ><code>&lt;output&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/progress"
            ><code>&lt;progress&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/Video"
            ><code>&lt;video&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/ul"><code>&lt;ul&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/ol"><code>&lt;ol&gt;</code></a
          >,
          <a href="/pt-BR/docs/Web/HTML/Element/li"><code>&lt;li&gt;</code></a
          >&nbsp;e muitos&nbsp;outros.
        </p>

        <p>
          Um elemento HTML é separado de outro texto em um documento por "tags",
          que consistem no nome do elemento entre "<code>&lt;</code>" e
          "<code>&gt;</code>". O nome de um elemento dentro de uma tag é
          insensível a maiúsculas e minúsculas. Isto é, pode ser escrito em
          maiúsculas, minúsculas ou um mistura. Por exemplo, a tag
          <code>&lt;title&gt;</code> pode ser escrita como
          <code>&lt;Title&gt;</code>, <code>&lt;TITLE&gt;</code> ou de qualquer
          outra forma.
        </p>

        <p>Os artigos abaixo podem ajudá-lo a aprender mais sobre HTML.</p>

        <section class="cleared" id="sect1">
          <ul class="card-grid">
            <li>
              <span>Introdução ao HTML</span>

              <p>
                Se você é novo no desenvolvimento web, não deixe de ler
                nosso&nbsp;artigo
                <a
                  href="/pt-BR/docs/Aprender/Getting_started_with_the_web/HTML_basico"
                  >O Básico do HTML </a
                >para aprender o que é HTML e como usá-lo.
              </p>
            </li>
            <li>
              <span>Tutoriais HTML </span>
              <p>
                Para artigos sobre como usar o HTML, assim como tutoriais e
                exemplos completos, confira&nbsp;nossa
                <a href="/pt-BR/docs/Aprender/HTML"
                  >Área de Aprendizado de&nbsp;HTML</a
                >.
              </p>
            </li>
            <li>
              <span>Referência do HTML </span>
              <p>
                Em nossa extensa seção&nbsp;de
                <a href="/pt-BR/docs/Web/HTML/ReferenciaHTML"
                  >Referência do HTML</a
                >, você encontrará os detalhes sobre cada&nbsp;elemento&nbsp;e
                atributo&nbsp;do HTML.
              </p>
            </li>
          </ul>

          <div class="row topicpage-table">
            <div class="section">
              <p>
                Nossa
                <a href="/pt-BR/docs/Aprender/HTML"
                  >Área de Aprendizado de HTML</a
                >&nbsp;apresenta vários módulos que ensinam HTML desde o
                início&nbsp;— sem necessidade de conhecimento&nbsp;prévio.
              </p>

              <dl>
                <dt>
                  <a href="/pt-BR/docs/Aprender/HTML/Introducao_ao_HTML"
                    >Introdução ao HTML</a
                  >
                </dt>
                <dd>
                  Este módulo fornece&nbsp;uma base de conhecimento, permitindo
                  que&nbsp;você acostume-se com&nbsp;os conceitos&nbsp;e
                  sintaxes importantes, tais como aplicar HTML
                  para&nbsp;texto,&nbsp;como criar <em>hyperlinks</em>&nbsp;e
                  como usar HTML para estruturar uma página web.
                </dd>
                <dt>
                  <a href="/en-US/docs/Learn/HTML/Multimedia_and_embedding"
                    >Multimídia e como incorporá-la</a
                  >
                </dt>
                <dd>
                  Este módulo explora como usar HTML para incluir multimídia em
                  sua página web, incluindo as diferentes maneiras para incluir
                  imagens, e como incorporar vídeos, áudios&nbsp;e até como
                  colocar uma página inteira em outras páginas web.
                </dd>
                <dt>
                  <a href="/en-US/docs/Learn/HTML/Tables">Tabelas HTML</a>
                </dt>
                <dd>
                  Representar dados tabulares em uma página web de maneira
                  entendível e acessível pode ser um desafio. Este módulo cobre
                  marcação tabular básica, junto com outras funcionalidades mais
                  complexas como legendas e sumários.
                </dd>
                <dt><a href="/en-US/docs/Learn/Forms">Formulários HTML</a></dt>
                <dd>
                  Formulários são partes muito importantes na Web&nbsp;—
                  proporcionam muitas das funcionalidades que você precisa para
                  interagir em páginas web. Exemplos disso são registrar-se e
                  conectar-se, enviar comentários, comprar produtos&nbsp;e muito
                  mais.&nbsp; Este módulo lhe permite&nbsp;começar a criar as
                  partes visíveis dos formulários, ou seja, as partes que os
                  usuários interagem.
                </dd>
                <dt>
                  <a href="/en-US/docs/Learn/HTML/Howto"
                    >Usando HTML para resolver problemas comuns</a
                  >
                </dt>
                <dd>
                  Apresenta&nbsp;links para seções com conteúdo explicando como
                  usar HTML para resolver problemas muito comuns, quando se cria
                  uma página web: como trabalhar com títulos, agregar imagens ou
                  vídeos, colocar ênfase em conteúdo, criar um formulário
                  básico, etc.
                </dd>
              </dl>

              <h2 id="Tópicos_avançados">Tópicos avançados</h2>

              <dl>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/CORS_enabled_image"
                    >Habilitando Imagem CORS
                  </a>
                </dt>
                <dd class="landingPageList">
                  O atributo
                  <code
                    ><a href="/en-US/docs/Web/HTML/Element/img#attr-crossorigin"
                      >crossorigin</a
                    ></code
                  >, em combinação com um&nbsp; cabeçalho&nbsp;<a
                    class="glossaryLink"
                    href="/en-US/docs/Glossary/CORS"
                    >CORS</a
                  >
                  adequado, permite definir imagens pelo elemento
                  <a href="/pt-BR/docs/Web/HTML/Element/img"
                    ><code>&lt;img&gt;</code></a
                  >&nbsp; para ser carregado de outras fontes em um elemento
                  <a href="/pt-BR/docs/Web/HTML/Element/canvas"
                    ><code>&lt;canvas&gt;</code></a
                  >&nbsp;como se estivessem sendo carregados da fonte atual.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Attributes/crossorigin"
                    >Configuração de atributos CORS</a
                  >
                </dt>
                <dd class="landingPageList">
                  Alguns elementos em HTML que suportam
                  <a href="/en-US/docs/Web/HTTP/CORS">CORS</a>, como&nbsp;por
                  exemplo o
                  <a href="/pt-BR/docs/Web/HTML/Element/img"
                    ><code>&lt;img&gt;</code></a
                  >
                  ou
                  <a href="/pt-BR/docs/Web/HTML/Element/Video"
                    ><code>&lt;video&gt;</code></a
                  >, têm o atributo&nbsp;<code>crossorigin</code> (a propriedade
                  <code>crossOrigin</code>), que permite configurar as
                  requisições CORS para os dados recebidos pelo elemento.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/API/Document/hasFocus"
                    >Gerenciamento de foco em HTML</a
                  >
                </dt>
                <dd class="landingPageList">
                  O atributo DOM
                  <code
                    ><a href="/en-US/docs/Web/API/Document/activeElement"
                      >activeElement</a
                    ></code
                  >
                  e o método DOM
                  <code
                    ><a href="/en-US/docs/Web/API/Document/hasFocus"
                      >hasFocus()</a
                    ></code
                  >&nbsp;lhe&nbsp;ajudam a melhorar a interação entre o usuário
                  e os elementos da página.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Link_types"
                    >Tipos de conexões</a
                  >
                </dt>
                <dd class="landingPageList">
                  Em HTML, vários tipos de links podem ser utilizados para
                  realizar conexões entre documentos, como por exemplo&nbsp;<a
                    href="/en-US/docs/Web/HTML/Element/a"
                    ><code>&lt;a&gt;</code></a
                  >,
                  <a href="/en-US/docs/Web/HTML/Element/area"
                    ><code>&lt;area&gt;</code></a
                  >&nbsp;e
                  <a href="/en-US/docs/Web/HTML/Element/link"
                    ><code>&lt;link&gt;</code></a
                  >.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/Media/Formats"
                    >Formatos de arquivos suportados pelos elementos de áudio e
                    vídeo em HTML</a
                  >
                </dt>
                <dd class="landingPageList">
                  Os elementos
                  <a href="/en-US/docs/Web/HTML/Element/audio"
                    ><code>&lt;audio&gt;</code></a
                  >
                  e
                  <a href="/en-US/docs/Web/HTML/Element/video"
                    ><code>&lt;video&gt;</code></a
                  >
                  permitem que você reproduza áudios e vídeos no seu HTML. Estes
                  elementos fornecem uma alternativa nativa de recursos do
                  navegador,&nbsp;parecidos com a do Adobe Flash Player e outros
                  plug-ins.
                </dd>
                <dt class="landingPageList">
                  <a
                    href="/en-US/docs/Web/HTML/Using_the_application_cache"
                    class="page-not-created"
                    title="This is a link to an unwritten page"
                    >Usando o cachê de aplicação</a
                  >
                </dt>
                <dd class="landingPageList">
                  O cachê de aplicação tem como o objetivo executar a aplicação
                  offline. Você pode usar a interface
                  <strong>Application Cache</strong> (<em>AppCache</em>) para
                  escolher elementos que o browser possa
                  guardar,&nbsp;deixando-os disponíveis para usuários
                  desconectados. As aplicações que forem armazenadas em cache
                  podem ser utilizadas sem nenhum problema, mesmo que o usuário
                  clique no botão de atualizar a página enquanto estiver
                  offline.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Preloading_content"
                    >Pré-carregando conteúdo com rel="preload"</a
                  >
                </dt>
                <dd class="landingPageList">
                  O valor <code>preload</code> do atributo&nbsp;<a
                    href="/pt-BR/docs/Web/HTML/Element/link#attr-rel"
                    ><code>rel</code></a
                  >
                  do elemento
                  <a href="/pt-BR/docs/Web/HTML/Element/link"
                    ><code>&lt;link&gt;</code></a
                  >
                  permite que você escreva solicitações de busca declarativas em
                  seu elemento
                  <a href="/pt-BR/docs/Web/HTML/Element/head"
                    ><code>&lt;head&gt;</code></a
                  >
                  do HTML, especificando recursos que suas páginas vão utilizar
                  logo após o carregamento. Você também pode necessitar que
                  estes recursos sejam pré-carregados ao início do ciclo de
                  execução da página e depois que o mecanismo de renderização do
                  navegador comece a agir. Isso garante que estarão disponíveis
                  mais cedo e que seja menos provável que sua página seja
                  bloqueada do processo de renderizar. Isso aumenta a
                  performance da página.&nbsp;Este artigo oferece um guia básico
                  de como <code>preload</code> funciona.
                </dd>
              </dl>
            </div>

            <div class="section">
              <h2 class="Documentation" id="Referências">Referências</h2>

              <dl>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Reference"
                    >Referência do HTML
                  </a>
                </dt>
                <dd class="landingPageList">
                  HTML é formado de <strong>elementos</strong>, onde cada um
                  deles pode ser modificado por alguns
                  <strong>atributos</strong>. Documentos HTML são
                  interconectados através de&nbsp;<a
                    href="/en-US/docs/Web/HTML/Link_types"
                    >links</a
                  >.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Element"
                    >Referência dos Elementos HTML</a
                  >
                </dt>
                <dd class="landingPageList">
                  Navegue em uma lista com todos os elementos
                  <a class="glossaryLink" href="/en-US/docs/Glossary/HTML"
                    >HTML</a
                  >.
                </dd>
              </dl>

              <dl>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Attributes"
                    >Referência de atributos em HTML</a
                  >
                </dt>
                <dd class="landingPageList">
                  Elementos em HTML possuem atributos. Estes configuram ou
                  ajustam o&nbsp;funcionamento e o comportamento dos seus
                  elementos de muitas maneiras.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Global_attributes"
                    >Atributos Globais</a
                  >
                </dt>
                <dd class="landingPageList">
                  Os atributos globais podem ser utilizados em todos os
                  <a href="/en-US/docs/Web/HTML/Element">elementos HTML</a>,
                  <em>mesmo aqueles que não são especificados como padrão</em>.
                  Isso significa que qualquer elemento não padrão vai permitir
                  estes atributos, mesmo que este retorne um documento HTML5
                  incompatível.
                </dd>
              </dl>

              <dl>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Inline_elements"
                    >Elementos em linha</a
                  >
                  e
                  <a href="/en-US/docs/Web/HTML/Block-level_elements"
                    >Elementos em nível de bloco</a
                  >
                </dt>
                <dd class="landingPageList">
                  Elementos HTML são normalmente elementos
                  <em>inline</em>&nbsp;(em linha) ou
                  <em>block-level</em>&nbsp;(em nível de bloco). Um elemento em
                  linha ocupa somente o espaço limitado pelas tags que o
                  definem. Um elemento em nível de bloco ocupa o espaço inteiro
                  do elemento pai (container), portanto criando um bloco.
                </dd>
              </dl>

              <dl>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Link_types">Tipos de Links</a>
                </dt>
                <dd class="landingPageList">
                  No HTML, vários tipos de links podem ser utilizados para
                  estabelecer e definir o relacionamento entre dois documentos.
                  Os tipos de elementos de link que podem ser incluídos são
                  <a href="/en-US/docs/Web/HTML/Element/a"
                    ><code>&lt;a&gt;</code></a
                  >,
                  <a href="/en-US/docs/Web/HTML/Element/area"
                    ><code>&lt;area&gt;</code></a
                  >&nbsp;e&nbsp;<code
                    ><a href="/en-US/docs/Web/HTML/Element/link"
                      >&lt;link&gt;</a
                    ></code
                  >.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/Media/Formats"
                    >Formatos de mídia suportados pelos elementos vídeo e áudio
                    do HTML</a
                  >
                </dt>
                <dd class="landingPageList">
                  Os elementos
                  <a href="/en-US/docs/Web/HTML/Element/audio"
                    ><code>&lt;audio&gt;</code></a
                  >&nbsp;e&nbsp;<a href="/en-US/docs/Web/HTML/Element/video"
                    ><code>&lt;video&gt;</code></a
                  >&nbsp;permitem que você reproduza mídias de áudio e vídeo.
                  Estes elementos fornecem uma alternativa nativa para o
                  navegador reproduzir recursos similares aos encontrados no
                  Adobe Flash e outros plug-ins.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/Guide/HTML/Content_categories"
                    >Tipos de conteúdo HTML</a
                  >
                </dt>
                <dd class="landingPageList">
                  O HTML é composto de uma grande diversidade de conteúdos, e
                  cada um pode ser usado em&nbsp;certos contextos&nbsp;e não ser
                  permitido&nbsp;em outros. Da mesma forma, cada um tem um
                  conjunto de outras categorias de conteúdos que podem ou não
                  serem usadas dentro deles. Este é um guia destas categorias.
                </dd>
                <dt class="landingPageList">
                  <a href="/en-US/docs/Web/HTML/Quirks_Mode_and_Standards_Mode"
                    >Os Modos Quirks e o Modo Padrão&nbsp;
                  </a>
                </dt>
                <dd class="landingPageList">
                  Informações sobre a história dos Modos&nbsp;<em>Quirks</em> e
                  o Modo padrão.
                </dd>
              </dl>

              <h2 class="landingPageList" id="Tópicos_relacionados">
                Tópicos relacionados
              </h2>

              <dl>
                <dt>
                  <a href="/en-US/docs/Web/HTML/Applying_color"
                    >Aplicando cores em elementos HTML usando CSS</a
                  >
                </dt>
                <dd>
                  Este artigo mostra as principais formas de se usar CSS para
                  adicionar cores ao seu conteúdo HTML, mostrando que partes dos
                  documentos HTML podem ser coloridas e quais propriedades
                  utilizar. Inclui exemplos e&nbsp;links para ferramentas de
                  <em>palette-building</em>, entre outros.
                </dd>
              </dl>
            </div>
          </div>
        </section>
      </div>
    </article>
  </div>
</template>